import React from "react";
import "./LoadingColorSpinner.css";

const LoadingColorSpinner = ({ size, height, stroke,  strokeW }) => {
	let classCircle = "spinnerPath",
		styleCircle = {};
	if (stroke) {
		classCircle = "spinnerPathWC";
		styleCircle = { stroke };
	}

	return (
		<div
			id="loadingSpinner"
			style={{
				position: "relative",
				width: size + "px" || "10px",
				height: size + "px" || "10px",
			}}>
			<svg className="spinnerSvg" viewBox="25 25 50 50">
				<circle
					className={classCircle}
					cx="50"
					cy="50"
					r="20"
					fill="none"
					style={styleCircle}
					strokeWidth={strokeW || 2}
					strokeMiterlimit="10"></circle>
			</svg>
		</div>
	);
};

export default LoadingColorSpinner;
